<template>
  <div id="retrieval">
    <div class="col-md-12">
      <div class="box" style="margin-top:15px;overflow: hidden">
        <div class="box-header">
          <h3 class="box-title"></h3>
          <!--<a :href="'http://'+this.$data.url" download="" class="btn btn-primary" style="float: right;color: #fff"></a>-->
        </div>
        <div class="box-body">
          <!---->
          <ul>
            <li class="same">
              <label>:</label>
              <el-input style="width: 130px" v-model="query.businessOrderNo" placeholder="" size="small"></el-input>
            </li>
            <li class="same">
              <label>:</label>
              <el-input style="width: 130px" v-model="query.orderNo" placeholder="" size="small"></el-input>
            </li>
            <li class="same">
              <label>:</label>
              <el-input style="width: 130px" v-model="query.sn" placeholder="" size="small"></el-input>
            </li>
            <li class="same">
              <label>:</label>
              <el-input style="width: 130px" v-model="query.userName" placeholder="" size="small"></el-input>
            </li>
            <li class="same">
              <label>:</label>
              <el-date-picker
                v-model="date"
                type="daterange"
                align="right"
                placeholder=""
                :picker-options="pickerOptions2" size="small">
              </el-date-picker>
            </li>
            <li class="same">
              <label>:</label>
              <el-select style="width: 120px" clearable v-model="query.withdrawStatus" size="small">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
              </el-select>
            </li>
            <li class="same">
              <div class="btn btn-primary" @click="search"></div>
            </li>
          </ul>
          <!---->
          <el-table v-loading.body="loading" height="583" style="font-size: 12px;margin:15px 0" :data="records" border>
            <el-table-column width="62" label="" fixed="left">
              <template scope="scope">
                <div>{{scope.$index+1}}</div>
              </template>
            </el-table-column>
            <el-table-column label="" min-width="112px">
              <template scope="scope">
                <span class="td" :data-clipboard-text="records[scope.$index].orderNo" type="text" size="small" style="cursor: pointer" title="">{{records[scope.$index].orderNo|changeHide}}</span>
              </template>
            </el-table-column>
            <el-table-column prop="userName" label="">
              <template scope="scope">
                <!--<router-link :to='"/admin/record/retrievalDet?idd="+records[scope.$index].idd+"&createTimes="+records[scope.$index].createTimes+"&idm="+records[scope.$index].idm'>{{records[scope.$index].userName}}</router-link>-->
                <router-link :to='{path:"/admin/record/retrievalDet",query:{idd:records[scope.$index].idd,createTimes:records[scope.$index].createTimes,idm:records[scope.$index].idm,businessOrderNo:records[scope.$index].businessOrderNo,orderNo:records[scope.$index].orderNo}}'>{{records[scope.$index].userName}}</router-link>
              </template>
            </el-table-column>
            <el-table-column prop="userType" label=""></el-table-column>
            <el-table-column label="">
              <template scope="scope">
                <span class="td" :data-clipboard-text="records[scope.$index].businessOrderNo" type="text" size="small" style="cursor: pointer" title="">{{records[scope.$index].businessOrderNo|changeHide}}</span>
              </template>
            </el-table-column>
            <el-table-column prop="tradeAmount" label="" align="right"></el-table-column>
            <el-table-column prop="poundage" label="" align="right"></el-table-column>
            <el-table-column prop="withdrawStatus" label=""></el-table-column>
            <el-table-column prop="payChannelName" label=""></el-table-column>
            <el-table-column label="" min-width="112">
              <template scope="scope">
                <span class="td" :data-clipboard-text="records[scope.$index].sn" type="text" size="small" style="cursor: pointer" title="">{{records[scope.$index].sn|changeHide}}</span>
              </template>
            </el-table-column>
            <el-table-column prop="createTimes" label="" width="162"></el-table-column>
            <el-table-column prop="updateTimes" label="" width="162"></el-table-column>
          </el-table>
          </el-table>
          <ul style="float: left;margin-top: 5px">
            <li>
              <label style="margin-right: 10px;"></label>
              <span>{{pageTotal}}&nbsp;</span>&nbsp;&nbsp;&nbsp;&nbsp;
              <span>{{addTotal}}&nbsp;</span>
            </li>
            <li>
              <label style="margin-right: 10px;"></label>
              <span>{{pageTotal1}}&nbsp;</span>&nbsp;&nbsp;&nbsp;&nbsp;
              <span>{{addTotal1}}&nbsp;</span>
            </li>
          </ul>
          <!---->
          <div class="block" style="text-align: right">
            <el-pagination @size-change="handleSizeChange"
                           @current-change="handleCurrentChange"
                           :current-page="query.pageNo"
                           :page-sizes="[10, 20, 50]"
                           :page-size="query.pageSize"
                           layout="total, sizes, prev, pager, next, jumper"
                           :total="count">
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="babel">
  import Clipboard from "clipboard"
  export default{
    name: 'deal',
    data(){
      return {
        query:{
          pageNo:1,
          pageSize:10,
          orderNo:'',
          businessOrderNo:'',
          sn:'',
          userName: '',
          startTime: '',
          endTime: '',
          withdrawStatus: '',
        },
        pageTotal:0,
        addTotal:0,
        pageTotal1:0,
        addTotal1:0,
        date: '',
        records: [],
        count: 0,
        total: '',
        loading: true,
        url: ''
      }
    },
    created: function () {
      var clipboard = new Clipboard('.td');
      // 
      clipboard.on('success', (e) => {
        this.$message({
          showClose: true,
          message: "  " + e.text,
          type: 'success'
        });
      });
      let time = new Date();
      this.date = [time,time];
      for (var j = 0; j < this.date.length; j++) {
        var str = this.date[j];
        var ary = [str.getFullYear(), str.getMonth() + 1, str.getDate()];
        for (var i = 0, len = ary.length; i < len; i++) {
          if (ary[i] < 10) {
            ary[i] = '0' + ary[i];
          }
        }
        str = ary[0] + '-' + ary[1] + '-' + ary[2];
        if (j == 0) {
          this.$data.query.startTime = str;
        } else {
          this.$data.query.endTime = str;
        }
      }
      this.getData();
      this.getAddTotal()
    },
    methods: {
      getData: function () {
        this.loading = true;
        this.$http.post('/admin/order/withdrawList',this.query)
          .then(function (res) {
            this.loading = false;
            this.$data.records = res.data.records;
            this.$data.count = res.data.count;
            var price=0,total=0;
            var toFix = function (val) {
              return parseFloat(val).toFixed(2)
            };
            for (var i = 0; i < this.records.length; i++) {
              price = toFix(parseFloat(price)+parseFloat(this.records[i].tradeAmount));
              total = toFix(parseFloat(total)+parseFloat(this.records[i].poundage));
              if (this.records[i].payRate != null) {
                this.records[i].payRate = (parseFloat(this.records[i].payRate) * 100).toFixed(2) + '%';
              }
            }
            this.pageTotal = price;
            this.pageTotal1 = total;
          },function (err) {
            this.$data.loading = false;
            this.$message({
              showClose: true,
              message: err.statusMessage,
              type: 'error'
            });
          })

      },
      //hss
      changeTime: function (row, column) {
        var val = row.createTime;
        if (val == '' || val == null) {
          return ''
        } else {
          val = new Date(val)
          var year = val.getFullYear();
          var month = val.getMonth() + 1;
          var date = val.getDate();
          var hour = val.getHours();
          var minute = val.getMinutes();
          var second = val.getSeconds();

          function tod(a) {
            if (a < 10) {
              a = "0" + a
            }
            return a;
          }

          return year + "-" + tod(month) + "-" + tod(date) + " " + tod(hour) + ":" + tod(minute) + ":" + tod(second);
        }
      },
      changeNum: function (row, column) {
        var val = row.tradeAmount;
        if(val!=''){
          return parseFloat(val).toFixed(2);
        }else {
          return val
        }

      },
      changeSettleStatus: function (row, column) {
        var val = row.settleStatus;
        if(val == 2){
          return ''
        }else if(val == 1){
          return ''
        }else if(val == 3){
          return ''
        }
      },
      search(){
        this.$data.query.pageNo = 1;
        this.getData()
        this.getAddTotal()
      },
      //
      handleSizeChange(val) {
        this.$data.query.pageNo = 1;
        this.$data.query.pageSize = val;
        this.getData()
      },
      //
      handleCurrentChange(val) {
        this.$data.query.pageNo = val;
        this.getData()
      },
      getAddTotal(){
        this.addTotal = 0;
        this.addTotal1 = 0;
        this.$http.post('/admin/order/withdrawAmount',this.query)
          .then(res=>{
            this.addTotal = res.data.tradeAmount;
            this.addTotal1 = res.data.poundage;
          })
          .catch(err=>{
            this.$data.loading = false;
            this.$message({
              showClose: true,
              message: err.statusMessage,
              type: 'error'
            });
          });
      }
    },
    watch: {
      date: function (val, oldVal) {
        if (val!=undefined&&val[0] != null) {
          for (var j = 0; j < val.length; j++) {
            var str = val[j];
            var ary = [str.getFullYear(), str.getMonth() + 1, str.getDate()];
            for (var i = 0, len = ary.length; i < len; i++) {
              if (ary[i] < 10) {
                ary[i] = '0' + ary[i];
              }
            }
            str = ary[0] + '-' + ary[1] + '-' + ary[2];
            if (j == 0) {
              this.$data.query.startTime = str;
            } else {
              this.$data.query.endTime = str;
            }
          }
        } else {
          this.$data.query.startTime = '';
          this.$data.query.endTime = '';
        }
      }
    },
    filters: {
      changeHide: function (val) {
        if(val!=""&&val!=null){
          val = val.replace(val.substring(3,val.length-6),"");
        }
        return val
      },
      changeStatus: function (val) {
        if(val == 1){
          return ""
        }else if(val == 3){
          return ""
        }else if(val == 4){
          return ""
        }else if(val == 5){
          return ""
        }else if(val == 6){
          return ""
        }else if(val == 7){
          return ""
        }else if(val == 6){
          return ""
        }
      },
    }
  }
</script>

<style scoped lang="less" rel="stylesheet/less">
  ul {
    padding: 0;
  }

  .same {
    list-style: none;
    display: inline-block;
    margin: 0 15px 15px 0;
  }
  .btn{
    font-size: 12px;
  }

  .price {
    display: inline-block;
    width: 210px;
    height: 30px;
    border-radius: 4px;
    border-color: #bfcbd9;
    position: relative;
    top: 6px;
    input {
      border: none;
      display: inline-block;
      width: 45%;
      height: 25px;
      position: relative;
      top: -3px;
    }
  }

  .price:hover {
    border-color: #20a0ff;
  }

</style>



